body {
  background: #000;
  padding: 2rem;
}

h2 {
  font-family: "Arial";
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 3rem;
  line-height: 1.15;
}

span {
  display: block;
}

span:not(.light) {
  opacity: 0;
  animation: flashText 0.5s ease-out alternate infinite;
}

span.light {
  position: relative;
  display: inline-block;
}
span.light:before {
  position: absolute;
  left: 0;
  top: -10%;
  width: 100%;
  height: 120%;
  background: #fff;
  filter: blur(10px);
  content: "";
  opacity: 0;
  animation: flash 0.5s ease-out alternate infinite;
}

@keyframes flash {
  to {
    opacity: 1;
  }
}
@keyframes flashText {
  to {
    opacity: 0.15;
  }
}

